/*  ,      */
.loader {
  position: absolute;
  left: 0;
  top: -100%;
  z-index: 15;
  overflow: hidden;
  width: 100%;
  height: 100%;
  box-sizing: border-box;
  color: #000000;
  background-color: rgba(0, 0, 0, 0.55);
  opacity: 0;
  transition: opacity 0.7s ease; }

.loader::after,
.loader::before {
  box-sizing: border-box; }

.loader.is-active {
  top: 0;
  opacity: 1; }

.loader-default::after {
  content: "";
  position: absolute;
  left: calc(50% - 24px);
  top: calc(50% - 24px);
  border: solid 8px #ffffff;
  border-left-color: transparent;
  border-radius: 50%;
  width: 48px;
  height: 48px;
  animation: rotation 1s linear infinite; }

@keyframes rotation {
  from {
    transform: rotate(0); }
  to {
    transform: rotate(359deg); } }

@keyframes blink {
  from {
    opacity: 0.5; }
  to {
    opacity: 1; } }

